.App {
  text-align: center;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(17px + 1vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

nav {
  background: #5aa185;
  padding: 15px;
  width: 20vw;
  position: absolute;
  left: 0px;
  height: 100vh;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

@media screen and (max-width: 870px) {
  nav {
    padding: 15px;
    width: 100vw;
    position: fixed;
    top: 0px;
    height: 60px;
    z-index: 1;
  }

  .desktopView {
    display: none;
  }

  .container {
    display: block !important;
  }

  .arrowContainer {
    display: block !important;
  }

  .landingWrapper {
    padding-left: 0px !important;
  }

  .landing, .about, .education, .experience, .skills, .interests {
    margin-top: 60px;
    width: 100vw !important;
    height: calc(100vh - 60px) !important;
  }

  .firstName, .lastName {
    font-size: 60px !important;
  }

  .landingWrapper, .experienceWrapper {
    padding: 20px !important;
    width: 90vw !important;
  }
}

@media screen and (max-width: 500px) {
  .firstName, .lastName {
    font-size: 45px !important;
  }
}

.landing, .about, .education, .experience, .skills, .interests {
  color: black;
  width: 80vw;
  position: absolute;
  right: 0px;
  height: 100vh;

  display: flex;
  flex-direction: column;
  align-items: flex-start;
  /* justify-content: center; */
}

nav div {
  padding: 20px;
}

a {
  color: white;
  text-decoration: none;
  font-size: calc(12px + 1vmin);
  text-shadow: 1px 2px 5px rgba(0, 0, 0, 0.47);
}

a:hover {
  color: rgba(255, 255, 255, 0.63);
}

.landingWrapper, .experienceWrapper {
  text-align: left; 
  padding: 40px;
  width: 80vw;
  margin: auto;
}

.imageWrapper {
  border-radius: 50%;
  /* padding: 5px; */
  /* margin: 20px; */
  width: 200px;
  height: 200px;
  background: rgba(255, 255, 255, 0.17);
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(13.6px);
  -webkit-backdrop-filter: blur(13.6px);
  border: 1px solid rgba(255, 255, 255, 0.53);
  padding: 5px;
}

.icons {
  font-size: 60px;
  margin-right: 30px;
}

.lightGray {
  color:rgba(0, 0, 0, 0.482)
}

.darkGray {
  color:rgba(0, 0, 0, 0.897)
}

.icons:hover {
  color: rgba(0, 0, 0, 0.689);
}

.firstName, .lastName {
  font-weight: 600;
  font-size: 80px;
}


.lastName, .email {
  color: #5aa185;
}














.arrowContainer {
  background-color: #5aa185;
  height: 60px;
  width: 60px;
  border-radius: 50%;
  position: fixed;
  bottom: 15px;
  right: 15px;
  color: white;
  display: none;
}

.arrow
{
  position: relative;
  bottom: -10px;
  left: 50%;
  margin-left:-20px;
  width: 40px;
  height: 40px;

  /**
   * Dark Arrow Down
   */
  background-image: url(data:image/svg+xml;base64,);
  background-size: contain;
}

.bounce {
  animation: bounce 6s infinite;
}

@keyframes bounce {
  0%, 20%, 50%, 80%, 100% {
    transform: translateY(0);
  }
  40% {
    transform: translateY(-30px);
  }
  60% {
    transform: translateY(-15px);
  }
}




/* GENERAL STYLES */
.visuallyHidden {
  position: absolute; 
  overflow: hidden; 
  clip: rect(0 0 0 0); 
  height: 1px; width: 1px; 
  margin: -1px; padding: 0; border: 0; 
}

